<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process137"></a>

<p><br></p>
<p><span class="rvts17">治疗之触（</span><span class="rvts50">The Healing Touch</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:437,Y:48</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：马提斯（</span><span class="rvts20">Matis</span><span class="rvts21">）、克劳德（</span><span class="rvts20">Klaud</span><span class="rvts21">）、朱莉斯（</span><span class="rvts20">Jules</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">治疗</span><span class="rvts20">3</span><span class="rvts21">个受伤的伤员</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1075.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">三个伤员的位置位于阿玛蒂亚避难所的入口处，治疗他们</span><span class="rvts20">3</span><span class="rvts21">个会获得额外的技能书和经验。另外，他们</span><span class="rvts20">3</span><span class="rvts21">个中的一些人也会加入到对抗亚历山大的队伍中。</span>
<br><span class="rvts21">这里需要注意的是，如果在治疗他们之前，已经抵达过了港口处的难民营聚集地（阿玛蒂亚所有战士将会聚集在那里），他们会死掉。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



